.container {
  position: relative;

  background-color: transparent;
  background-image: linear-gradient(135deg, transparent, 40%, var(--element-color), 60%, transparent);
  background-repeat: no-repeat;
  background-position: 100% 100%;
  background-size: 200% 200%;
  overflow: hidden;
  
  border-radius: 40px;

  width: 100%;
  height: 100%;

  animation: load 5s alternate infinite linear;

  &Loaded {
    animation: none;
    background-image: none;
  }
}

@keyframes load {
  from {
    background-position: 100% 100%;
  }
  to   {
    background-position: 0% 0%;
  }
}

.image{
  opacity: 0;
  transition: opacity 0.5s ease-out;

  &Loaded {
    opacity: 1;
  }
}