@use '/src/style/setting' as s;

.about {
  padding-top: 40px;

  &_main_info {
    display: grid;
    grid-template-columns: minmax(400px, 2fr) minmax(400px, 1fr);
    align-items: center;
  }

  &_info_left {
    grid-column: 1 / 2;

  }

  &_info_right {
    grid-column: 2 / 3;
  }

  &_card {
    border-radius: 12px;
    padding: 24px;
    background-color: var(--element-color);
    font-size: 1.1rem;
    line-height: 2rem;

    &_text {  
      margin-top: 20px;
    }

    &_header {
      display: flex;
      align-items: center;
      font-size: 1.2rem;
      gap: 20px;
    }
  }

  &_cardS {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
    margin-top: 20px;

    &_header {
      margin-top: 40px;
    }
  }

  &_text {
    line-height: 2rem;
    margin-top: 20px;
  }

  &_header {
    font-size: 2rem;
  }

  &_photo {
    width: 100%;
    height: 100%;
  }
}

.photo_container {
  width: 400px;
  height: 400px;
}


@media (max-width: s.$container-max-widths-lg) {
  .about {
    &_main_info {
      grid-template-columns: minmax(300px, 1fr);
    }

    &_info_left {
      grid-column: 1 / 2;
  
    }
  
    &_info_right {
      grid-column: 1 / 2;
      
    }
  }

  .photo_container {
    margin: 0 auto {
      top: 20px;
    };
  }
}


@media (max-width: s.$container-max-widths-md) {
  .about {
    &_cardS {
      grid-template-columns: 1fr;
    }
  }
}

@media (max-width: s.$container-max-widths-sm) {
  .photo_container {
    width: 300px;
    height: 300px;
  }
}